<template>
	<view>
		<cu-custom bgColor="theme-bg-color" :isBack="true">
			<block slot="backText">返回</block>
			<block slot="content">座位预约</block>
		</cu-custom>
		<form>
			<view class="cu-form-group">
				<view class="title">座位</view>
				<input placeholder="请输入座位" :disabled="true" name="roomSeatName" v-model="formData.roomSeatName"></input>
			</view>
			<view class="cu-form-group">
				<view class="title">预约人</view>
				<input placeholder="请输入预约人" name="userName" v-model="formData.userName"></input>
			</view>
			<view class="cu-form-group">
				<view class="title">联系方式</view>
				<input placeholder="请输入联系方式" name="phone" v-model="formData.phone"></input>
			</view>
			<view class="cu-form-group">
				<view class="title">预约日期</view>
				<picker mode="date" :value="formData.startDate" :start="beginDate" :end="endDate"
					@change="appointDateChange">
					<view class="picker">
						{{formData.startDate}}
					</view>
				</picker>
			</view>
			<view class="cu-form-group">
				<view class="title">开始时间</view>
				<picker mode="time" :value="startTime" start="00:00" end="23:59" @change="appointTimeChange">
					<view class="picker">
						{{formData.startTime}}
					</view>
				</picker>
			</view>
<view class="cu-form-group">
				<view class="title">结束时间</view>
				<picker mode="time" :value="endTime" start="00:00" end="23:59" @change="endTimeChange">
					<view class="picker">
						{{formData.endTime}}
					</view>
				</picker>
			</view>
			<view class="cu-form-group align-start">
				<view class="title">备注</view>
				<textarea maxlength="-1" placeholder="请输入备注内容" v-model="formData.content"></textarea>
			</view>
			<view class="padding margin-top">

				<button class="cu-btn block theme-bg-color margin-tb-sm lg" @click="Save(0)">预约</button>

			</view>
			
			<view class="cu-bar bg-white solid-bottom margin-top">
				<view class="action">
					<text class="cuIcon-title text-red"></text>该座位参与预约的还有
				</view>
			</view>
			<view class="cu-list menu-avatar" v-if="list.length>0">
				<template v-for="(item,index) in list">
					<view class="cu-item">
						 <view class="cu-avatar round lg" :style="[{ backgroundImage:'url(' + item.avatar+ ')' }]">
						</view> 
						<view class="content">
							<view class="text-grey">预约人:{{item.userName}}</view>
							<view class="text-gray text-sm flex">
								<view class="text-cut">
									<text class="cuIcon-time text-red  margin-right-xs"></text>
									{{item.appointTime|formatDateTime}}
								</view>
								<view class="text-cut">
									<text class="cuIcon-time text-red  margin-right-xs"></text>
									~{{item.appointEndTime|formatDateTime}}
								</view>
								
							</view>
						</view>
						<view class="action">
							 <view class="text-red text-xs">{{"预约成功"}}</view> 
							<view class="cu-tag round bg-red sm"></view>
						</view>
					</view>
				</template>
			</view>
			
		</form>
	</view>
</template>

<script>
	var startDate = new Date();
	let year = startDate.getFullYear();
	let month = startDate.getMonth() + 1;
	let day = startDate.getDate();
	var beginDate = `${year}-${month}-${day}`;

	var endDate = `${year+1}-${month}-${day}`;

	import store from "@/store";
	import {
		mapGetters
	} from "vuex";
	export default {
		data() {
			return {
				detail: {},
				id: "",
				beginDate: beginDate,
				endDate: endDate,
				list:[],
				formData: {
					status: 0,
					startDate: beginDate,
					startTime: "00:00",
					endTime:"00:00",
					momentId: 0,

				}
			}
		},
		computed: {
			...mapGetters(["userInfo"]),
		},
		async onLoad(options) {
            this.options = options
			if (this.$comm.CheckToken()) {
				if (this.options.id) {
					console.log(this.options.id)
					var detail = uni.getStorageSync(this.options.id + "");
					this.detail = JSON.parse(detail);
					this.id = this.options.id;
					this.formData.roomSeatName = this.detail.name;
                    this.formData.roomSeatId=this.detail.id;
					this.formData.roomName=this.options.roomName;
					this.formData.roomId=this.options.roomId;
					await this.getDetailApi();
				}
			}
		},
		async onShow() {


		},
		methods: {
			async getDetailApi() {
				let url = "/make/an/appoint/findByModal";
				let {
					success,
					data
				} = await this.$http.PostAsync(url, {
					roomSeatId: this.id,
					status:1,
				});
				console.log(data);
				if (success == true) {
					this.list = data.list;
				}
			},
			async appointDateChange(e) {
				console.log(e);
				this.formData.startDate = e.detail.value;
			},
			async appointTimeChange(e) {
				console.log(e);
				this.formData.startTime = e.detail.value;
			},
			async endTimeChange(e) {
				console.log(e);
				this.formData.endTime = e.detail.value;
			},


			async Save(type) {

				if (!this.formData.userName) {
					await this.$comm.ShowToast("联系人不能为空");
					return
				}
				if (!this.formData.phone) {
					await this.$comm.ShowToast("联系方式不能为空");
					return
				}

				this.formData.appointTime = `${this.formData.startDate} ${this.formData.startTime}:00`;
				this.formData.appointEndTime = `${this.formData.startDate} ${this.formData.endTime}:00`;

				await this.$http.PostReturnPage("/make/an/appoint/add", this.formData);

			},




		}
	}
</script>

<style scoped>
	.publish {
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #1CBBB4;
		width: 100upx;
		height: 100upx;
		border-radius: 50%;
		position: fixed;
		right: 30upx;
		bottom: 150upx;
	}
</style>
